<template>
  <div class='container-fluid'>
    <div class='container-fluid'>
      <div class='jumbotron' style='background-color: rgb(39, 76, 119); color: white;'>
        <h1 class='display-4'>Self-Made Data Scientist</h1>
        <p class='lead'>
          Welcome to my blog!
        </p>
        <p>
          Here, we will discuss best practices within Data Science, Machine Learning,
          and Software Development.  We will also cover career suggestions. 
        </p>
      </div>
    </div>

    <div class='card-columns'>
      <div class='card border-light' v-for="(item, index) in entries" :key="index">
        <div class='card-title' @click="$router.push({name: item.name})">
          <h3>
            {{item.title}}
          </h3>
          <h4>
            {{item.date}}
          </h4>
        </div>

        <p class='card-text'>{{item.description}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import BLOGENTRIES from '@/statics/blogs.json'

export default {
  name: 'Home',
  computed: {
    entries() {
      return BLOGENTRIES
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
h3 {
  color: #42b883;
  margin-bottom: 0;
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
  .subtitle {
    color: grey;
    font-size: .98rem;
    float: right;
    font-weight: normal;
  }
}
p {
  margin-top: .4rem;
}

</style>



