/* esversion: 6 */

import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Blogs from '@/statics/blogs.json'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    ...Blogs.map(entry => ({
      path: `/${entry.id}`,
      name: entry.name,
      component: () => import(`@/markdown/${entry.name}.md`)
    }))
  ]
})



