<template>
  <div id="app">
    <img src="./assets/logo.png">
    <router-view/>
    <p>
      Social:

      <a href="https://twitter.com/PyScaleLLC?ref_src=twsrc%5Etfw" class="twitter-follow-button" data-show-count="false">Follow @PyScaleLLC</a>
    </p>
    <Facebook :url="'blog.pyscale.net/#' + $router.history.current.path" scale="3"></Facebook>

    <Reddit :url="'blog.pyscale.net/#' + $router.history.current.path" scale="3" title="Share #pyscale blog"></Reddit>

    <Linkedin :url="'blog.pyscale.net/#' + $router.history.current.path" scale="3"></Linkedin>

    <Twitter :url="'blog.pyscale.net/#' + $router.history.current.path" title="Share #pyscale blog" scale="3">
    </Twitter>
  </div>
</template>
<script async src="https://platform.twitter.com/widgets.js" charset="utf-8"></script>
<script>
import 'bootstrap/dist/css/bootstrap.css'
import {
  Facebook,
  Twitter,
  Linkedin,
  Reddit,
} from 'vue-socialmedia-share'

export default {
  name: 'App',
  components: {
    Facebook,
    Twitter,
    Linkedin,
    Reddit
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

#code-block{
  text-align: left;
  background-color: #D3D3D3;
  color: #000000;
}
</style>



